@keyframes floating{0%{transform:translateY(0px)}50%{transform:translateY(-10px)}100%{transform:translateY(0px)}}@keyframes scale-in{0%{transform:scale(0);opacity:1}100%{transform:scale(1);opacity:1}}*{box-sizing:border-box}html,body{background-color:#0e1112;color:#c5c8c9;font-family:"DM Sans",sans-serif}a{color:#84a0c6;transition:.3s ease-in-out}a:hover,a:focus{color:#758cb0}.button{border:unset;color:#c5c8c9;background-color:transparent;border:3px solid #292d32 !important;margin:6px !important;transform:translateY(0em);transition:.4s}.button:hover,.button:focus{color:#c5c8c9;box-shadow:inset 3.25em 0 0 0 #292d32,inset -3.25em 0 0 0 #292d32;border-color:#292d32 !important;transform:translateY(-0.5em)}.content{text-align:left;font-size:20px;width:100%}.content h1,.content h2,.content h3,.content h4,.content h5,.content h6{color:#c5c8c9}.card{color:#c5c8c9;background-color:#0e1112;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji;animation:scale-in .7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}.card .repo-title{font-weight:bold}.card .repo-description{font-size:14px}.navbar{margin:8px 16px;background-color:#101415}.navbar .navbar-brand .navbar-burger,.navbar .navbar-item{color:#c5c8c9}.navbar .navbar-brand,.navbar .navbar-menu{background-color:#0e1112}.navbar .navbar-end .navbar-item{background:none;margin:0px 12px;border-bottom:3px solid transparent;transition:.3s ease-in-out}.navbar .navbar-end .navbar-item:hover,.navbar .navbar-end .navbar-item:focus{color:#c5c8c9;border-color:#8dc776}.navbar .navbar-end .is-icon{margin:0px 4px !important}.navbar .navbar-end .is-icon .is-hidden-desktop{margin:0px 8px !important}.footer{color:#c5c8c9;background-color:#0e1112;padding:4em !important}.footer p{font-size:16px}.blue-text{color:#84a0c6}#introduction{flex-direction:unset}#introduction .introduction-container{display:flex;flex-direction:column;justify-content:center;align-items:center}#introduction .introduction-container .avatar{margin-top:-0.5em !important}#introduction .introduction-container .avatar .image-title{border-radius:100%;height:16em !important;width:16em !important;max-width:none !important;animation:3s ease-in-out infinite;animation-name:floating}#introduction .introduction-container .content{margin-top:2em;margin-bottom:2em}#projects{background-color:#101415}#projects .projects-container{display:flex;flex-direction:column;justify-content:center;align-items:center}#projects .projects-container h1{color:inherit !important;font-size:36px;font-weight:bold;align-items:center;margin-bottom:1em}#about .about-container .about-description{color:#c5c8c9;font-size:18px;font-weight:normal}#about .about-container .columns{padding:12px 0px 12px 0px}@media only screen and (max-width: 600px){#about .about-container .columns .column p{margin-left:0px !important}}@media only screen and (min-width: 600px){#about .about-container .columns .column p{margin-left:2.5em !important}}#about .about-container .columns .column a{color:#c5c8c9}#about .about-container h1{color:inherit !important;font-size:36px !important;border-bottom:#8dc776 solid 4px;display:inline-block}#not-found .not-found-container{color:#c5c8c9;padding:8em;display:flex;flex-direction:column;justify-content:center;align-items:center}#not-found .not-found-container .not-found-title{font-size:3em;margin-bottom:1em}#not-found .not-found-container .not-found-description{text-align:center;font-size:1.3em;color:#c5c8c9}
